import React, { Component } from 'react';
import Head from '../components/Head';
import axios from 'axios';
import config from '../config';
class Index extends Component {
    constructor(props) {
        super(props);
        this.state = {
            arrDares: [],
            currentDare: '',
            loading: false,
            startTime: 0,
            currentTime: 0
        };
    }
    componentDidMount() {
        this.setState({ loading: true })
        const access_token = config.access_token || config.access_token2
        const query = `https://graph.facebook.com/${config.post_id}/comments?fields=message,from&access_token=${access_token}`
        try {
            this.getAllComments(query)
        } catch (error) {
            console.log(error + '')
        }
    }
    getAllComments = async (query) => {
        const aa = await this.checkToken(config.access_token)
        console.log(aa)
        return
        axios.get(query)
            .then(res => {
                const resData = res.data
                const { data, paging } = resData
                const dataFiltter = data.filter(item => (!item.message.includes('Gọi điện')))
                this.setState({ arrDares: this.state.arrDares.concat(dataFiltter) })
                if (paging && paging.next) {
                    this.getAllComments(paging.next)
                } else {
                    this.setState({ loading: false })
                }
            }).catch(e => {
                throw new Error('co van de')
            })

    }
    checkToken = access_token => {
        const query = 'https://graph.facebook.com/me?access_token=' + access_token
        return axios.get(query)
    }
    sleep = ms => new Promise(resolve => setTimeout(resolve, ms));


    generateDare = async () => {
        if (this.state.loading) return
        this.setState({ loading: true })
        await this.sleep(3000)
        const random = Math.floor(Math.random() * this.state.arrDares.length)
        this.setState({ currentDare: this.state.arrDares[random], loading: false })
    }
    render() {
        const { currentDare, loading, startTime, currentTime } = this.state
        const fromName = currentDare.from ? currentDare.from.name : ''
        const style = {
            container: {
                height: '100vh',
            },
            tren: {
                width: '100%',
                height: '70%',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                backgroundColor: 'blue'
            },
            duoi: {
                width: '100%',
                height: '30%',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                backgroundColor: 'green',
            }
        }
        return (
            <div className="">
                <Head></Head>
                <div style={style.container} >
                    <h6 className="center-align">Develope by @xuanhuyjs</h6>
                    <div style={style.tren}>
                        {!loading ? (<div>
                            <div className="card blue-grey darken-1">
                                <div className="card-content white-text">
                                    <p>{currentDare.message || 'Welcome to #DAREHUB'}</p>
                                </div>
                                <div className="card-action right-align">
                                    <span>{fromName}</span>
                                </div>
                            </div>
                        </div>) : null}

                    </div>
                    {loading ? (<div className="progress" >
                        <div className="indeterminate"></div>
                    </div>) : null}
                    <div style={style.duoi} className="center-align" onClick={this.generateDare} >
                        <a className="waves-effect waves-light btn-large">generate dare</a>
                    </div>
                </div>

            </div>
        );
    }
}

export default Index;
